@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,400&family=Prata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Old+Standard+TT:ital,wght@0,400;0,700;1,400&family=Prata&display=swap);
@import url(https://fonts.googleapis.com/css2?family= Noto+Sans,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Old+Standard+TT:ital,wght@0,400;0,700;1,400&family=Prata&display=swap);
*,
::before,
::after {
  box-sizing: border-box;
  border-width: 0;
}

:root {
  font-size: 16px;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}

body {
  background: #f6f7fb;
  color: #334680;
  font-family: 'Roboto', system-ui, -apple-system, BlinkMacSystemFont,
    'Segoe UI', 'Helvetica Neue', Arial, 'Noto Sans', sans-serif;
  min-height: 100vh;
  margin: 0;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

a {
  color: inherit;
  text-decoration: none;
  background-color: transparent;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  outline: 2px solid transparent;
  outline-offset: 2px;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

h2,
h3,
h4,
h5,
h6,
hr,
figure,
p,
pre {
  margin: 0;
}

button {
  background-color: transparent;
  background-image: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.app {
  padding: 100px 500px;
  display: flex;
  flex-direction: column;
  height: 100hv;
}

.app__footer {
  display: flex;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #a9a9a9;
  align-items: center;
  justify-content: center;
  margin-top: 140px;
}

@media (max-width: 1204px) {
  .app {
    padding: 100px 300px;
  }
}
@media (max-width: 768px) {
  .app {
    padding: 50px 150px;
  }
}
@media (max-width: 640px) {
  .app {
    padding: 10px;
  }
}

.uploader {
  display: flex;
  flex-direction: column;
  background: #ffffff;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  justify-content: center;
  padding: 36px 32px;
  align-items: center;
  font-family: Poppins;
  font-style: normal;
  grid-gap: 19px;
  gap: 19px;
  width: 400.36px;
}

.uploader > h1 {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  letter-spacing: -0.035em;
  color: #4f4f4f;
}

.uploader > h3 {
  font-weight: 500;
  font-size: 10px;
  line-height: 15px;
  letter-spacing: -0.035em;
  color: #828282;
}

.uploader__container {
  display: flex;
  flex-direction: column;
  width: 338px;
  height: 218.9px;
  background: #f6f8fb;
  border: 1px dashed #97bef4;
  box-sizing: border-box;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  grid-gap: 46px;
  gap: 46px;
}

.uploader__container-drag {
  border: 1px dashed green !important;
}

.container__title {
  font-size: 12px;
  line-height: 18px;
  letter-spacing: -0.035em;
  color: #bdbdbd;
}

.uploader__form {
  display: flex;
  width: 101px;
  height: 31.98px;
  background: #2f80ed;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.form__file {
  display: none;
}

.uploader__form > label {
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  letter-spacing: -0.035em;
  color: #ffffff;
  cursor: pointer;
}

.uploader__success {
  color: green;
}

.success__text {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  letter-spacing: -0.035em;
  color: #4f4f4f;
}

.loading {
  display: flex;
  flex-direction: column;
  background: #ffffff;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  padding: 36px 32px;
  grid-gap: 50px;
  gap: 50px;
  width: 400.36px;
}

.loading > h3 {
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  letter-spacing: -0.035em;
  color: #4f4f4f;
}

.loading__progress {
  display: flex;
  height: 10px;
  background: #f2f2f2;
  border-radius: 8px;
  align-items: center;
  width: 100%;
}

.progress__number {
  height: 100%;
  background: #2f80ed;
  border-radius: 8px;  
}

.uploaded {
  display: flex;
  flex-direction: column;
  background: #ffffff;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  justify-content: center;
  padding: 36px 32px;
  align-items: center;
  font-family: Poppins;
  font-style: normal;
  grid-gap: 19px;
  gap: 19px;
  width: 400.36px;
}

.uploaded__success {
  color: green;
}

.success__text {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  letter-spacing: -0.035em;
  color: #4f4f4f;
}

.uploaded__container {
  display: flex;
  flex-direction: column;
  width: 338px;
  height: 218px;
  background: #f6f8fb;
  border: 1px dashed #97bef4;
  box-sizing: border-box;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  grid-gap: 46px;
  gap: 46px;
}

.uploaded__container > img {
  max-width: 100%;
  max-height: 100%;
  border-radius: 12px;
  align-items: center;
  justify-content: center;
  object-fit: cover;
}

.uploaded__copy {
  display: flex;
  background: #f6f8fb;
  border: 1px solid #e0e0e0;
  box-sizing: border-box;
  border-radius: 8px;
  align-items: center;
  padding: 5px;
  grid-gap: 15px;
  gap: 15px;
  width: 338px;
}

.uploaded__copy > span {
  flex: 1 1;
  font-weight: 500;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: -0.035em;
  color: #4f4f4f;
}

.button__copy {
  width: 74px;
  height: 30px;
  background: #2f80ed;
  border-radius: 8px;
  font-weight: 500;
  font-size: 10px;
  line-height: 12px;
  text-align: center;
  letter-spacing: -0.035em;
  color: #ffffff;
  cursor: pointer;
}

